@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;


body {
    background: black;
    color: white;
}

.pulse-scale {
    animation: pusle-scale 0.75s infinite alternate;
}


@keyframes pusle-scale {

    to {
        transform: scale(1.025);
    }
}
